.menu_container{
    position: absolute;
    background-color: white;
    width: 100vw;
    height: 100vh;
    z-index: 9;
    animation: showingmenu .6s ease-in-out;
    left: 0
    hr{
        margin: 0;
    }

    .top_menumostrado{
        display:flex;
        flex-direction: row;
        align-items: center;
        img.logo{
            width:90px;
            margin:auto;
        }
    }
}

.hidden{
    animation: hiddenMenu .4s ease-in-out;
    left:-100vw;
}

ul.menu_lista{
    list-style-type: none;
    margin: 0;
    padding: 0;
    p{
        margin: 0;
        padding: 7px 0 7px 20px;
    }
    li{
        ul{
            list-style: none;
            margin: 0;
            padding: 0;
            li:active{
                background-color: rgba(0, 0, 0, 0.1);
            }

            p{
                padding-left: 40px
            }
        }
    }

    
}

@keyframes showingmenu {
    0% {left: -100vw}
    100% {left: 0%}
}

@keyframes hiddenMenu {
    0% {left: 0%}
    100% {left: -100vw}
}