.header_container{
    ul.header_lista{
        display: flex;
        flex-direction: row;
        list-style-type: none;
        align-items: center;
        justify-content: space-between;
        padding: 0;
        margin: 4px 7px;

        img.logo{
            width: 90px;
        }
        button{
            border: none;
            background-color: transparent;
        }

        .searcher{
            display: flex;
            align-items: center;
            .searcherdiv{
                display: flex;
                align-items: center;
                border: 1px solid rgba(0, 0, 0, .5);
                border-radius: 3px;
                margin-left: 8px;

                .iconsearch{
                    margin: 5px 6px 0px 4px;
                }
            }

            input.buscador{
                padding: 6px 6px;
                border: none;
                border-radius: 3px;
                outline: none;
                width: 100%;
                font-size: 14px;
            }
        }
    }
}