.footer_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    background-color: var(--footer-bg-dark);

    .redes_container{
        
        ul.redes_lista{
            display: flex;
            flex-direction: row;
            list-style-type: none;
            padding: 0;
            width: 50%;
            margin: auto;
            justify-content: space-evenly;
            li:active{
                color: var(--color-green);
            }
        }

    }

    .opciones_container{
        background-color: var(--bg-black);
        ul{
            display: flex;
            flex-direction: row;
            justify-content: center;
            flex-wrap: wrap;
            list-style-type: none;
            padding: 0;
            width: 92%;
            margin: 20px auto;

            p{
                margin: 2px 0;
                font-size: 14px;
                padding: 0 3px;
            }
            p:hover{
                text-decoration: underline;
            }
        }
    }
    .derechos_reservados{
        font-size: 14px;
        text-align: center;
        font-style: italic; 
    }
}